@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom fonts */


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.base-container{
    width: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.base-container .header{
        font-size: 24px;
        font-family: 'Open Sans', sans-serif;
}

.base-container .content {
    display: flex;
    flex-direction: column;
    border: 1px solid #808080;
    padding: 20px;
    border-radius: 20px;
}

.base-container .content .image {
    width: 21em;    
}

.base-container .content .image img{
    width: 100%;
    height: 100%;
}

.base-container .content .form{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.base-container .content .form-group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.base-container .content .form-group input{
    margin-top: 6px;
    min-width: 18em;
    height: 37px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

.base-container .content .form-group input:focus {
    outline: none;
    box-shadow: 0px 12px 0.8px #0e81ce96;
}

.base-container .content .form-group label {
    font-size: 20px;
}

.base-container .footer{
    margin-top: 3em;
}

.base-container .content button{
    height: 35px;
    margin-left: 30px;
    margin-right: 30px;
}

.base-container .logout{
    margin-left: auto;
}

.base-container .logout .form-group{
    display: flex;
}

.base-container .logout .form-group .lang-group{
    display: inline-block;
    flex-direction: column;
    align-items: flex-start;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 10px;
}

.base-container .logout .form-group .logout-group{
    display: inline-block;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
